import { FullCalendarView } from "../../common/component/Calendar";
import { IOption } from "../../common/component/DropDownMenu";
import { id } from "../../common/types/domain";
import {
  StudentProfileNavigation, 
  AbonementPageNavigation, 
  DetailsDrawerShown, 
  StudentNavigation, 
  ScheduleView, 
  AbonementOfferShownTarget,
  AbonementLessonsFilter,
  AdditionalDrawerShown,
  StudentSearchOptions
} from "../store/modal";
import { selectShowcaseAbonementOffers } from "./sellableItems";
import { StudentSelector } from "./types";


export const selectStudentNavigation: StudentSelector<StudentNavigation> = (state) => state.modal.studentNavigation;
export const selectStudentProfileNavigation: StudentSelector<StudentProfileNavigation> = (state) => state.modal.studentProfileNavigation;
export const selectAbonementPageNavigation: StudentSelector<AbonementPageNavigation> = (state) => state.modal.abonementPageNavigation;

export const selectStudentDetailsDrawerShown: StudentSelector<DetailsDrawerShown> = (state) => state.modal.detailsDrawerShown;
export const selectStudentAbonementShowcaseShown: StudentSelector<AbonementOfferShownTarget> = (state) => state.modal.abonementOffersShowcaseShown;
export const selectAdditionDrowerShown: StudentSelector<AdditionalDrawerShown> = (state) => state.modal.additionalDrawerShown;

export const selectIsShowMore: StudentSelector<boolean> = (state) => state.modal.showMore;

export const selectCurrentDate: StudentSelector<Date> = (state) => state.modal.currentDate;
export const selectCurrentSubjectId: StudentSelector<id> = (state) => state.modal.currentSubjectId || "";
export const selectCurrentTeacherId: StudentSelector<id> = (state) => state.modal.—ÅurrentTeacherId || "";

export const selectCurrentOwnAbonementId: StudentSelector<id> = (state) => state.modal.currentOwnAbonementId || "";
export const selectCurrentAbonementOfferIndex: StudentSelector<number> = (state) => state.modal.currentAbonementOfferIndex || 0;
export const selectCurrentAbonementOffersIds: StudentSelector<id[]> = (state) => state.modal.currentAbonementOffersIds || [];

export const selectCurrentAbonementOfferId: StudentSelector<id> = (state) => {
  const currentAbonementOffers = selectShowcaseAbonementOffers(state);
  const currentAbonementOfferIndex = selectCurrentAbonementOfferIndex(state);

  const currentAbonement = currentAbonementOffers[currentAbonementOfferIndex];

  return currentAbonement.id;
};

export const selectShowcaseAbonementOffersLastIndex: StudentSelector<number> = (state) => {
  const currentAbonementOffers = selectShowcaseAbonementOffers(state);
  const lastIndex = currentAbonementOffers.length - 1;
  return lastIndex;
};

export const photoHeight = 60;
export const avatarHeight = photoHeight - 16;
export const descriptionDefaultHeight = 120;
export const showMoreDescriptionHeight = descriptionDefaultHeight + 180;

export const selectSubjectDescriptionHeight: StudentSelector<number> = (state) => {
  const showMore = selectIsShowMore(state);
  const descriptionHeight = showMore ? showMoreDescriptionHeight : descriptionDefaultHeight;
  return descriptionHeight;
};

export const selectScheduleView: StudentSelector<ScheduleView> = (state) => state.modal.scheduleView;

export const selectStudentFullCalendarView: StudentSelector<FullCalendarView> = (state) => {

  const scheduleView = selectScheduleView(state);

  switch (scheduleView) {
    case ScheduleView.schedule:
      return FullCalendarView.listWeek;;
    case ScheduleView.week:
      return FullCalendarView.timeGridWeek;
    default:
      return FullCalendarView.timeGridWeek;
  }
};

export const selectStudentSearch: StudentSelector<string> = (state) => state.modal.search;
export const selectStudentSearchOption: StudentSelector<StudentSearchOptions> = (state) => state.modal.searchOption;
export const selectStudentSearchOptions: StudentSelector<IOption[]> = () => [
  {
    id: StudentSearchOptions.all,
    title: 'All',
  },
  {
    id: StudentSearchOptions.subject,
    title: 'Subject',
  },
  {
    id: StudentSearchOptions.teacher,
    title: 'Teacher',
  },
];
export const selectAbonementLessonsFilter: StudentSelector<AbonementLessonsFilter> = (state) => state.modal.currentAbonementLessonsFilter;

export const selectAbonementHeaderHasFilter: StudentSelector<boolean> = (state) => {
  const abonementPageView = selectAbonementPageNavigation(state);

  switch (abonementPageView) {
    case AbonementPageNavigation.subjects:
      return false;
    case AbonementPageNavigation.lessons:
      return true;
    default:
      return false;
  }
};
