import { IExternalResourceState } from "../../common/store/externalResourceSlice";
import { IAbonementOffer, ILesson, IRule, LessonsPreviewSource } from "../../common/types/domain";
import { ISubjectView, ITeacherView } from "../../common/types/teacherDomainView";
import { getPreviewLessonsFromASubject, isIntersect } from "../../common/utilities/RulePreviewCalendarFunctions/getPreviewLessons";
import { getRuleFormatted } from "../../common/utilities/getRuleFormatted";
import { AbonementLessonsFilter } from "../store/modal";
import { selectAttendedLessonIds } from "./domain";
import { selectAbonementLessonsFilter, selectCurrentDate, selectCurrentSubjectId } from "./novigation";
import { StudentSelector } from "./types";

export const selectExternalLessons: StudentSelector<IExternalResourceState<ILesson>> = (state) => state.externalLessons;
export const selectScheduledLessons: StudentSelector<ILesson[]> = (state) => selectExternalLessons(state).data;

export const selectExternalTeacher: StudentSelector<ITeacherView> = (state) => state.externalTeacher;

export const selectExternalTeacherName: StudentSelector<string> = (state) => selectExternalTeacher(state).name;
export const selectExternalTeacherAvatar: StudentSelector<string> = (state) => selectExternalTeacher(state).avatar;
export const selectExternalTeacherDescription: StudentSelector<string> = (state) => selectExternalTeacher(state).description;

export const selectTeacherSubjetsAmount: StudentSelector<number> = (state) => selectExternalTeacher(state).subjects.length; 
export const selectTeacherAbonementsAmount: StudentSelector<number> = (state) => selectExternalTeacher(state).abonementOffers.length;

export const selectTeacherSubjectsForStudent: StudentSelector<ISubjectView[]> = (state) => selectExternalTeacher(state).subjects;

export const selectExternalTeachers: StudentSelector<ITeacherView[]> = (state) => state.externalTeachers.data;
export const selectExternalSubjects: StudentSelector<ISubjectView[]> = (state) => state.externalSubjects.data;

export const selectHasTeachers: StudentSelector<boolean> = (state) => selectExternalTeachers(state).length > 0;
export const selectHasSubjects: StudentSelector<boolean> = (state) => selectExternalSubjects(state).length > 0;
export const selectHasSearchResults: StudentSelector<boolean> = (state) => selectHasTeachers(state) || selectHasSubjects(state);
export const selectExternalSubject: StudentSelector<ISubjectView | undefined> = (state) => {

  const subjects = selectTeacherSubjectsForStudent(state);
  const currentSubjectId = selectCurrentSubjectId(state);

  const currentSubject = subjects.find((subject) => subject.id === currentSubjectId);

  return currentSubject;
};

export const selectSubjectName: StudentSelector<string> = (state) => {
  const externalSubject = selectExternalSubject(state);
  return externalSubject?.name || "";
}

export const selectSubjectDescription: StudentSelector<string> = (state) => {
  const externalSubject = selectExternalSubject(state);
  return externalSubject?.description || "";
}

export const selectSubjectRules: StudentSelector<IRule[]> = (state) => {
  const externalSubject = selectExternalSubject(state);
  console.log(externalSubject?.rules);
  return externalSubject?.rules || [];
};

export const selectSubjectScheduleRepresentation: StudentSelector<string> = (state) => {
  const externalSubjectRules = selectSubjectRules(state);
  const scheduleRepresentation = getRuleFormatted(externalSubjectRules);

  return scheduleRepresentation;
}

export const selectSubjectSchedule: StudentSelector<ILesson[]> = (state) => {
  const externalSubject = selectExternalSubject(state);
  if (!!externalSubject) {
    const externalLessons = selectScheduledLessons(state);
    const currentDate = selectCurrentDate(state);
    const subjectPreviewLessons = getPreviewLessonsFromASubject(externalSubject, LessonsPreviewSource.thisSubjectSchedule,  currentDate );
    const previewSchedule = [...externalLessons, ...subjectPreviewLessons];
    return previewSchedule;
  } else {
    return [];
  };  
};

export const selectExternalSubjectHasIntersect: StudentSelector<boolean> = (state) => {
  const externalSubject = selectExternalSubject(state);
  if (!!externalSubject) {
    const externalLessons = selectScheduledLessons(state);
    const currentDate = selectCurrentDate(state);
    const subjectPreviewLessons = getPreviewLessonsFromASubject(externalSubject, LessonsPreviewSource.thisSubjectSchedule,  currentDate );
    
    const isScheduleIntersect = isIntersect(externalLessons, subjectPreviewLessons);
    return isScheduleIntersect;
  } else {
    return false;
  }; 
};

export const selectAbonementLessonsFiltered: StudentSelector<ILesson[]> = (state) => {

  const abonementLessonsFilter = selectAbonementLessonsFilter(state);
  const allLessons = selectScheduledLessons(state);

  switch (abonementLessonsFilter) {
    case AbonementLessonsFilter.all: {
      return allLessons;
    } 
    case AbonementLessonsFilter.attended: {
      const attendedLessonIds = selectAttendedLessonIds(state);
      return allLessons.filter((lesson) => attendedLessonIds.includes(lesson.id));
    }
    default: {
      return allLessons;
    }
  }
};

export const selectTeacherAbonementOffers: StudentSelector<IAbonementOffer[]> = (state) => selectExternalTeacher(state).abonementOffers;

