import { Typography } from "@mui/material";
import { HOC } from "../../../common/hoc/types";
import { useStudentSelector } from "../../store";
import AllInclusiveIcon from '@mui/icons-material/AllInclusive';
import { photoHeight } from "../../selectors/novigation";
import { MetricItemContainer, MetricsContainer } from "../../../common/component/MetricStyled";
import { 
  selectAbonementExpiryDateRepresentation, 
  selectAbonementLessonsLeft, 
  selectAbonementLessonsLeftShown, 
  selectAmountAttendedLessons 
} from "../../selectors/domain";

export const AbonementInfo: HOC = () => {

  const expiryDateRepresentation = useStudentSelector(selectAbonementExpiryDateRepresentation);
  const amountLessonsAttended = useStudentSelector(selectAmountAttendedLessons);

  const showLessonsLeft = useStudentSelector(selectAbonementLessonsLeftShown);

  const amountLessonsLeft = useStudentSelector(selectAbonementLessonsLeft);

  return (
    <MetricsContainer height={photoHeight}>
        <MetricItemContainer>
          <Typography textAlign="center">{expiryDateRepresentation}</Typography>
          <Typography textAlign="center">expiry date</Typography>                
        </MetricItemContainer>
        <MetricItemContainer>
            <Typography textAlign="center">{amountLessonsAttended}</Typography>
            <Typography textAlign="center">lessons attended</Typography>
        </MetricItemContainer>
        <MetricItemContainer>
          {
            showLessonsLeft ? 
            <>
              <Typography textAlign="center">{amountLessonsLeft}</Typography>
              <Typography textAlign="center">lessons left</Typography>
            </> : <AllInclusiveIcon  color="success"/>
          }
        </MetricItemContainer>
    </MetricsContainer>
  );
}
