import { put, select } from "redux-saga/effects";
import { createCommand } from "../../common/utilities/createCommand";
import { id } from "../../common/types/domain";
import { AbonementOfferShownTarget, DetailsDrawerShown, studentModalSlice } from "../store/modal";
import { selectIsShowMore } from "../selectors/novigation";
import { requestTeacherViewEffect, setCurrentTeacherIdBySubjectIdEffect } from "./teacherPageForStudentsCommandsEffects";

export const closeDetailesDrower = createCommand<void>(
  'closeDetailesDrower',
  function* ({ payload }) {
    yield put(studentModalSlice.actions.setModalState({ detailsDrawerShown: DetailsDrawerShown.closed }));
  }
);

export const toggleSeeMore = createCommand<void>(
  'toggleSeeMore',
  function* ({ payload }) {
    const currentShowMore = yield select(selectIsShowMore)
    yield put(studentModalSlice.actions.setModalState({ showMore: !currentShowMore }));
  }
);

export const navigateAbonementShowcaseBySubjectId = createCommand<id>(
  'navigateAbonementShowcaseBySubjectId',
  function* ({ payload }) {
    yield put(studentModalSlice.actions.setModalState({ currentSubjectId: payload}));
    yield setCurrentTeacherIdBySubjectIdEffect();
    yield put(studentModalSlice.actions.setModalState({ abonementOffersShowcaseShown: AbonementOfferShownTarget.subject }));
  }
);

export const startTeacherPageForStudent = createCommand<void>(
  'startTeacherPageForStudent',
  function* ({ payload }) {
    yield requestTeacherViewEffect();
  }
);
