import { HOC } from '../../../common/hoc/types';
import { 
  selectAdditionDrowerShown, 
  selectStudentAbonementShowcaseShown, 
  selectStudentDetailsDrawerShown, 
  selectStudentNavigation 
} from '../../selectors/novigation';
import { 
  AdditionalDrawerShown,
  DetailsDrawerShown, 
  StudentNavigation,
  AbonementOfferShownTarget 
} from '../../store/modal';
import { StudentProfilePage } from '../StudentProfilePage';
import { SearchPage } from '../SearchPage';
import { AbonementPage } from '../AbonementPage';
import { AbonementShowcase } from '../AbonementShowcase';
import { useStudentSelector } from '../../store';
import { StudentFooter } from '../StudentFooter';
import { StudentSchedule } from '../StudentSchedule';
import { TeacherPageForStudentModal } from '../TeacherPageForStudentModal';
import { SubjectPageForStudentModal } from '../SubjectPageForStudentModal';

export const AuthenticatedStudentApp: HOC = () => {
  return (
    <>
      <StudentWindow />
      <StudentFooter/>
      <StudentDetailsDrawer />
      <StudentAdditionDrawer />
      <AbonementShowCase />
    </>
  );
}

const StudentWindow: HOC = () => {

  const navigation: StudentNavigation = useStudentSelector(selectStudentNavigation);

  switch (navigation) {
      case StudentNavigation.schedule: {
          return  <StudentSchedule/>
      }
      case StudentNavigation.studentProfilePage: {
          return <StudentProfilePage />
      }
      case StudentNavigation.searchPage: {
          return <SearchPage />
      }
      default: return <></>
  }
}

const StudentDetailsDrawer: HOC = () => {
  const detailsDrawerShown = useStudentSelector(selectStudentDetailsDrawerShown);

  switch (detailsDrawerShown) {
    case DetailsDrawerShown.teacherPageForStudent: {
      return <TeacherPageForStudentModal />
    }

    case DetailsDrawerShown.abonementPageForStudent: {
      return <AbonementPage />
    }

    case DetailsDrawerShown.closed: {
      return <></>;
    }
    default: return <></>;
  }
};

const AbonementShowCase: HOC = () => {
  const showcaseShown = useStudentSelector(selectStudentAbonementShowcaseShown);
  const isShown = showcaseShown !== AbonementOfferShownTarget.closed;

  return isShown ? <AbonementShowcase /> : <></>;
};

const StudentAdditionDrawer: HOC = () => {
  const additionDrawerShown = useStudentSelector(selectAdditionDrowerShown);
  switch (additionDrawerShown) {
    case AdditionalDrawerShown.subjectPageForStudent: {
      return <SubjectPageForStudentModal />
    }
    case AdditionalDrawerShown.closed: {
      return <></>;
    }
    default: return <></>;
  }
};
