import { fork } from "redux-saga/effects";
import * as studentSearchPageCommands from './studentSearchPageCommands'
import { watchCommandChapter } from "../../common/utilities/createCommand";
import * as authenticationCommands from "./authentication";
import * as studentsStudentScheduleCommands from "./studentsStudentScheduleCommands";
import * as studentProfilePageCommands from "./studentProfilePageCommands";
import * as teacherPageForStudentCommands from "./teacherPageForStudentsCommands";
import * as abonementShowcasePageCommands from "./abonementShowcasePageCommands";
import * as subjectPageForStudentsCommands from "./subjectPageForStudentsCommands";
import * as abonementPageCommands from "./abonementPageCommands";

export const studentRootSaga = function*() {
  yield fork(watchCommandChapter(authenticationCommands));
  yield fork(watchCommandChapter(studentSearchPageCommands));
  yield fork(watchCommandChapter(studentsStudentScheduleCommands));
  yield fork(watchCommandChapter(studentProfilePageCommands));
  yield fork(watchCommandChapter(teacherPageForStudentCommands));
  yield fork(watchCommandChapter(abonementShowcasePageCommands));
  yield fork(watchCommandChapter(subjectPageForStudentsCommands));
  yield fork(watchCommandChapter(abonementPageCommands));
};
