import { AbonementType, id } from "../../common/types/domain";
import { IAbonementConsumableView, IStudentView } from "../../common/types/studentDomainView";
import { ISubjectView } from "../../common/types/teacherDomainView";
import { selectCurrentOwnAbonementId } from "./novigation";
import { StudentSelector } from "./types";

export const selectStudentDomain: StudentSelector<IStudentView> = (state) => state.studentDomain;
export const selectStudentAvatar: StudentSelector<string> = (state) => {

  const avatarUrl = selectStudentDomain(state).avatar;

  return avatarUrl;
};

export const selectCurrentStudentName: StudentSelector<string> = (state) => selectStudentDomain(state).name;

export const selectStudentProfileAbonements: StudentSelector<IAbonementConsumableView[]> = (state) => selectStudentDomain(state).abonementConsumables;

export const selectStudentProfileAbonementsIds: StudentSelector<id[]> = (state) => selectStudentProfileAbonements(state).map((abonement) => abonement.id);

export const selectStudentAbonentsAmount: StudentSelector<number> = (state) => selectStudentProfileAbonements(state).length;


export const selectStudentProfileSubjects: StudentSelector<ISubjectView[]> = (state) => {
  const studentConsumable = selectStudentProfileAbonements(state);
  const allSsubjects = studentConsumable.flatMap((consumable) => consumable.subjects);
  const uniqueSubjects = [...new Set(allSsubjects)];
  return uniqueSubjects;
};

export const selectStudentSubjectsAmount: StudentSelector<number> = (state) => selectStudentProfileSubjects(state).length;

export const selectCurrentOwnAbonement: StudentSelector<IAbonementConsumableView | undefined> = (state) => {
  const currentOwnAbonementId = selectCurrentOwnAbonementId(state);
  const studentConsumable = selectStudentProfileAbonements(state);

  const currentOwnAbonement = studentConsumable.find((consumable) => consumable.id === currentOwnAbonementId);

  return currentOwnAbonement;
};

export const selectCurrentOwnAbonementType: StudentSelector<AbonementType | undefined> = (state) => {
  const currentOwnAbonement = selectCurrentOwnAbonement(state);
  const currentOwnAbonementType = currentOwnAbonement?.type;
  return currentOwnAbonementType;
};

export const  selectCurrentOwnAbonementSubjects: StudentSelector<ISubjectView[]> = (state) => {
  const currentOwnAbonement = selectCurrentOwnAbonement(state);

  if (!currentOwnAbonement) {
    return [];
  } else {
    const currentOwnAbonementSubjects = currentOwnAbonement.subjects;
    return currentOwnAbonementSubjects;
  }
};

export const selectAbonementExpiryDate: StudentSelector<Date> = (state) => {
  
  const currentOwnAbonement = selectCurrentOwnAbonement(state);

  if (!currentOwnAbonement) {
    return new Date();
  } else {
    const dateStart = new Date(currentOwnAbonement.dateStart);
    const limitTime = currentOwnAbonement.limitTime;

    const expiryDate = new Date(dateStart.setMonth(dateStart.getMonth() + limitTime));
    return expiryDate;
  }
};

export const selectAbonementExpiryDateRepresentation: StudentSelector<string> = (state) => {

  const expiryDate = selectAbonementExpiryDate(state);

  const dateFormatter = new Intl.DateTimeFormat('en-US', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
  });

  const dateRepresentation = dateFormatter.format(expiryDate);

  return dateRepresentation;
};

export const selectAttendedLessonIds: StudentSelector<id[]> = (state) => {
  const currentOwnAbonement = selectCurrentOwnAbonement(state);

  if (!currentOwnAbonement) {
    return [];
  } else {
    const currentOwnAbonementAttendetLessonIds = currentOwnAbonement.lessonsAttended;
    return currentOwnAbonementAttendetLessonIds;
  }
};

export const selectAmountAttendedLessons: StudentSelector<number> = (state) => {

  const attendetLessonIds = selectAttendedLessonIds(state);
  const amountAttendedLessons = attendetLessonIds.length;

  return amountAttendedLessons;
};

export const selectAbonementLimitLessons: StudentSelector<number> = (state) => {
  const currentOwnAbonement = selectCurrentOwnAbonement(state);

  if (!currentOwnAbonement) {
    return 0;
  } else {
    const limitLessons = currentOwnAbonement.limitLessons;
    return limitLessons;
  }
};

export const selectAbonementLessonsLeft: StudentSelector<number> = (state) => {

  const amountAttendedLessons = selectAmountAttendedLessons(state);
  const abonementLimitLessons = selectAbonementLimitLessons(state);

  const abonementLessonsLeft = abonementLimitLessons - amountAttendedLessons;

  return abonementLessonsLeft;
};

export const selectAbonementLessonsLeftShown: StudentSelector<boolean> = (state) => {

  const abonementType = selectCurrentOwnAbonementType(state);
  const abonementLessonsLeftShown = abonementType === AbonementType.limited;

  return abonementLessonsLeftShown;
};
