import { put } from "redux-saga/effects";
import { createCommand } from "../../common/utilities/createCommand";
import { AdditionalDrawerShown, studentModalSlice } from "../store/modal";
import { requestTeacherViewEffect, setCurrentTeacherIdBySubjectIdEffect } from "./teacherPageForStudentsCommandsEffects";

export const closeAdditionDrower = createCommand<void>(
  'closeAdditionDrower',
  function* () {
    yield put(studentModalSlice.actions.setModalState({ additionalDrawerShown: AdditionalDrawerShown.closed }));
  }
);

export const startSubjectPageForStudent = createCommand<void>(
  'startSubjectPageForStudent',
  function* () {
    yield setCurrentTeacherIdBySubjectIdEffect();
    yield requestTeacherViewEffect();
  }
);
