import { IAbonementOffer } from "../../common/types/domain";
import { AbonementOfferShownTarget } from "../store/modal";
import { selectTeacherAbonementOffers } from "./external";
import { selectCurrentSubjectId, selectStudentAbonementShowcaseShown, selectCurrentAbonementOffersIds } from "./novigation";
import { StudentSelector } from "./types";

export const selectShowcaseAbonementOffers: StudentSelector<IAbonementOffer[]> = (state) => {
  const showcase = selectStudentAbonementShowcaseShown(state);
  switch (showcase) {
    case AbonementOfferShownTarget.teacher: {
      const teacherAbonementOffers = selectTeacherAbonementOffers(state);
      return teacherAbonementOffers;
    }
    case AbonementOfferShownTarget.subject: {
      const currentSubjectId = selectCurrentSubjectId(state);
      const allOffers = selectTeacherAbonementOffers(state);

      const offersThatSatisfy = allOffers.filter((offer) => offer.subjects.includes(currentSubjectId));
      return offersThatSatisfy;
    }
    case AbonementOfferShownTarget.abonementOffers: {
      const allOffers = selectTeacherAbonementOffers(state);
      const currentAbonementOffersIds = selectCurrentAbonementOffersIds(state);

      const offersThatSatisfy = allOffers.filter((offer) => currentAbonementOffersIds.includes(offer.id));
      return offersThatSatisfy;
    }
    default: {
      return [];
    }
  }
};
