import { put } from "redux-saga/effects";
import { createCommand } from "../../common/utilities/createCommand";
import { AbonementLessonsFilter, AbonementPageNavigation, studentModalSlice } from "../store/modal";

export const navigateAbonementPageSubjects = createCommand<void>(
  'navigateAbonementPageSubjects',
  function* () {
    yield put(studentModalSlice.actions.setModalState({abonementPageNavigation: AbonementPageNavigation.subjects}));
  }
);

export const navigateAbonementPageLessons = createCommand<void>(
  'navigateAbonementPageLessons',
  function* () {
    yield put(studentModalSlice.actions.setModalState({abonementPageNavigation: AbonementPageNavigation.lessons}));
  }
);

export const filterAbonementAttendedLessons = createCommand<void>(
  'filterAbonementAttendedLessons',
  function* () {
    yield put(studentModalSlice.actions.setModalState({currentAbonementLessonsFilter: AbonementLessonsFilter.attended}));
  }
);

export const filterAbonementAllLessons = createCommand<void>(
  'filterAbonementAllLessons',
  function* ({ payload }) {
    yield put(studentModalSlice.actions.setModalState({currentAbonementLessonsFilter: AbonementLessonsFilter.all}));
  }
);
