import { call, put, select } from "redux-saga/effects";
import { ISubject, id } from "../../common/types/domain";
import { selectCurrentSubjectId, selectCurrentTeacherId } from "../selectors/novigation";
import { ITeacherView } from "../../common/types/teacherDomainView";
import { domainHandelers, readTeacherViewApiCall } from "../../common/api/domainApi";
import { externalTeacherSlice } from "../store/externalTeacher";
import { studentModalSlice } from "../store/modal";

export function* requestTeacherViewEffect () {
  const currentTeacherId: id = yield select(selectCurrentTeacherId)
  const teacherView: ITeacherView = yield call(readTeacherViewApiCall, currentTeacherId);
  yield put(externalTeacherSlice.actions.setExternalTeacher(teacherView));
}
export function* setCurrentTeacherIdBySubjectIdEffect () {
  const currentSubjectId: id = yield select(selectCurrentSubjectId);
  const subjectData: ISubject = yield call(domainHandelers.subjectApiHandeler.single, currentSubjectId);

  const teacherId = subjectData.teacherId;
  yield put(studentModalSlice.actions.setModalState({ —ÅurrentTeacherId: teacherId }));
}
