import { call, put, select } from "redux-saga/effects";
import { IAbonementConsumableView, IStudentView } from "../../common/types/studentDomainView";
import { selectEditableStudentName } from "../selectors/editable";
import { selectStudentDomain, selectStudentProfileAbonementsIds } from "../selectors/domain";
import { IAbonementConsumable, IRule, IStudent, ISubject, id } from "../../common/types/domain";
import { domainHandelers } from "../../common/api/domainApi";
import { selectOwnId } from "../selectors/dom";
import { IDataBaseRule } from "../../common/api/handelers";
import { ISubjectView } from "../../common/types/teacherDomainView";
import { mapDataBaseRuleToRule } from "../../common/store/utils";
import { studentDomainViewSlice } from "../store/studentView";

export function* saveStudentName() {
  const studentName: string   = yield select(selectEditableStudentName);
  const studentView: IStudentView = yield select(selectStudentDomain);
  const studentAbonementsIds: id[] = yield select(selectStudentProfileAbonementsIds)
  const newStudentView: IStudent  = {...studentView, name: studentName, abonementConsumables: studentAbonementsIds};
  
  yield call(domainHandelers.studentAPIHandeler.update, newStudentView);
}

export function* updateStudentDomain() {
  const studentId: id = yield select(selectOwnId);
  const student: IStudent = yield call(domainHandelers.studentAPIHandeler.single, studentId);

  const studentAbonementConsumablesIds = student.abonementConsumables;
  const studentAbonementConsumables: IAbonementConsumable[] = yield call(domainHandelers.abonementConsumable.list, '', {ids: studentAbonementConsumablesIds});
  const subjectIds: id[] = studentAbonementConsumables.flatMap(({subjects})=> subjects );
  const subjects: ISubject[] = yield call(domainHandelers.subjectApiHandeler.list,'', {ids: subjectIds});

  const subjectRulesIds: id[] = subjects.flatMap(({rules}) => rules);
  const rules: IDataBaseRule[] = yield call(domainHandelers.rulesApiHandeler.list, '', {ids: subjectRulesIds});

  const subjectViews = subjects.map((subject): ISubjectView => {
    const thisSubjectRules = subject.rules.map((id) => rules.find(({id: itemId}) => itemId === id));
    const viewDataBaseRules = thisSubjectRules.filter((item) => !!item) as IDataBaseRule[];
    const viewRules: IRule[] = viewDataBaseRules.map(mapDataBaseRuleToRule);

    return {
      ...subject,
      rules: viewRules,
    }
  })

  const studentAbonementConsumablesViews: IAbonementConsumableView [] = studentAbonementConsumables.map((abonement)=> {

    const abonementSubjectsViews = subjectViews.filter((subject)=> abonement.subjects.includes(subject.id));

    return {
      ...abonement,
      subjects: abonementSubjectsViews,
    }
  })

  const studentView: IStudentView = {
    id: student.id,
    name: student.name,
    avatar: student.avatar,
    abonementConsumables: studentAbonementConsumablesViews
  };

  yield put(studentDomainViewSlice.actions.setDomainView({...studentView}))
}
