import { call, put } from "redux-saga/effects";
import { createCommand } from "../../common/utilities/createCommand";
import { ScheduleView, StudentNavigation, studentModalSlice } from "../store/modal";
import { studentEditableSlice } from "../store/editableResources";


export const setScheduleView = createCommand<ScheduleView>(
  'setScheduleView',
  function* ({ payload }) {
    yield put(studentModalSlice.actions.setModalState({ scheduleView: payload }));
  }
);

export const navigateShedule = createCommand<void>(
  'navigateShedule',
  function* () {
    yield put(studentModalSlice.actions.setModalState({ studentNavigation: StudentNavigation.schedule }));
    yield resetEditingStudent();
  }
);

export const navigateSearch = createCommand<void>(
  'navigateSearch',
  function* () {
    yield put(studentModalSlice.actions.setModalState({ studentNavigation: StudentNavigation.searchPage }));
    yield resetEditingStudent();
  }
);

export const navigateStudentPrifile = createCommand<void>(
  'navigateStudentPrifile',
  function* () {
    yield put(studentModalSlice.actions.setModalState({ studentNavigation: StudentNavigation.studentProfilePage }));
  }
);

export const startStudentApp = createCommand<void>(
  'startStudentApp',
  function* () {
    yield call(console.log, 'startStudentApp');
  }
);

function* resetEditingStudent() {
  yield put(studentEditableSlice.actions.reset());
}