import { ReactNode } from "react";
import { AbonementOfferSellableItem } from "../../../common/component/AbonementOfferSellableItem";
import { Drawer } from "../../../common/component/Drawer";
import { ModalHeader } from "../../../common/component/ModalHeader";
import { SellableItemsCarousel } from "../../../common/component/SellableItemsCarousel";
import { SlideoutDirection } from "../../../common/component/types";
import { HOC } from "../../../common/hoc/types";
import { Box, Button } from "@mui/material";
import { AbonementType } from "../../../common/types/domain";
import { useStudentSelector } from "../../store";
import { selectCurrentAbonementOfferIndex } from "../../selectors/novigation";
import { selectTeacherSubjectsForStudent } from "../../selectors/external";
import { selectShowcaseAbonementOffers } from "../../selectors/sellableItems";

export const AbonementShowcase: HOC = () => {

  const currentItemIndex = useStudentSelector(selectCurrentAbonementOfferIndex);

  const abonementOffers = useStudentSelector(selectShowcaseAbonementOffers);
  const subjectViews = useStudentSelector(selectTeacherSubjectsForStudent);

  const mostPopularAbonementOfferIds = ['AbonementOfferId0', 'AbonementOfferId4'];

  const salleableItems: ReactNode[] = abonementOffers.map((abonementOffer) => {

    const isMostPopular = mostPopularAbonementOfferIds.includes(abonementOffer.id);
    const isUnlimited = abonementOffer.type === AbonementType.unlimited;

    return(
      <AbonementOfferSellableItem
        key={abonementOffer.id}
        id={abonementOffer.id}
        limitLessons={abonementOffer.limitLessons}
        limitTime={abonementOffer.limitTime}
        price={abonementOffer.price}
        subjects={subjectViews}
        isUnlimited={isUnlimited}
        isMostPopular={isMostPopular}
        onClick={() => console.log(`onClick ${abonementOffer.id}`)}
      />
    )
  });


  return (
    <Drawer
      isOpen
      slideoutDirection={SlideoutDirection.left}
      comandChilden={<ModalHeader title={'–°hoose your Abonement'} onClick={() => console.log('onClose')}/>}
    >
      <SellableItemsCarousel 
        currentItemIndex={currentItemIndex} 
        items={salleableItems} 
        onItemChange={(index) => {console.log(`change item to ${index}`)} }        
      />
      <EnrollButton/>
    </Drawer>
  );
}

const EnrollButton: React.FC = () => {
  return (
    <Box 
      width='100%'
      display='flex'
      justifyContent='center'
      marginBottom={'3rem'}
      zIndex={1800}
    >
      <Button
        size="large"
        variant='contained' 
        color= 'success'
        onClick={() => console.log ('Enroll ')}
      >
        {`Enroll`}
      </Button>
    </Box>
  );
}
