import { call } from "redux-saga/effects";
import { IStudent } from "../../../common/types/domain";
import { domainHandelers } from "../../../common/api/domainApi";
import { mapFirebaseUser } from "../../../common/utilities/mapFirebaseUser";
import { User } from "firebase/auth";
import { FirebaseUserInfo } from "../../../common/types/utility";

export const createNewUserFromUserEffect = function*(user: User) {
  const firebaseUserInfo = mapFirebaseUser(user.providerData[0]);
  yield call(createNewStudentFromProfileEffect, firebaseUserInfo);
};

export const createNewStudentFromProfileEffect = function*({ id, displayName, photoURL }: FirebaseUserInfo) {
  if (id) {
    const currentStudent: IStudent = yield call(domainHandelers.studentAPIHandeler.single, id);
    const isExistingStudent = currentStudent && currentStudent.name && currentStudent.avatar && currentStudent.abonementConsumables;
    if (!isExistingStudent) {
      const currentUserStudent: IStudent = {
        id,
        name: displayName || '',
        avatar: photoURL || '',
        abonementConsumables: [],
      }
      yield call(domainHandelers.studentAPIHandeler.update, currentUserStudent);
    }
  }
}
