import { call, put, select } from "redux-saga/effects";
import { createCommand } from "../../common/utilities/createCommand";
import { id } from "../../common/types/domain";
import { AbonementOfferShownTarget, studentModalSlice } from "../store/modal";
import { selectCurrentAbonementOfferId, selectCurrentAbonementOfferIndex, selectShowcaseAbonementOffersLastIndex, } from "../selectors/novigation";
import { requestTeacherViewEffect } from "./teacherPageForStudentsCommandsEffects";

export const closeAbonementShowcase = createCommand<void>(
  'closeAbonementShowcase',
  function* () {
    yield put(studentModalSlice.actions.setModalState({abonementOffersShowcaseShown: AbonementOfferShownTarget.closed}));
  }
);

export const abonementOfferForward = createCommand<void>(
  'abonementOfferforward',
  function* () {
    const currentAbonementOfferIndex = yield select(selectCurrentAbonementOfferIndex);
    const lastIndex = yield select(selectShowcaseAbonementOffersLastIndex);

    if (currentAbonementOfferIndex === lastIndex) {
      yield put(studentModalSlice.actions.setModalState({currentAbonementOfferIndex: 0}));
    } else {
      yield put(studentModalSlice.actions.setModalState({currentAbonementOfferIndex: currentAbonementOfferIndex + 1}));
    }
  }
);

export const abonementOfferBack = createCommand<void>(
  'abonementOfferBack',
  function* () {
    const currentAbonementOfferIndex = yield select(selectCurrentAbonementOfferIndex)

    if (currentAbonementOfferIndex === 0) {
      const lastIndex = yield select(selectShowcaseAbonementOffersLastIndex);
      yield put(studentModalSlice.actions.setModalState({currentAbonementOfferIndex: lastIndex}));
    } else {
      yield put(studentModalSlice.actions.setModalState({currentAbonementOfferIndex: currentAbonementOfferIndex - 1}));
    }
  }
);

export const startAbonementShowcasePage = createCommand<void>(
  'startAbonementShowcasePage',
  function* () {
    yield requestTeacherViewEffect();
  }
);

export const onAbonementOfferClick = createCommand<id>(
  'onAbonementOfferClick',
  function* ({ payload }) {
    yield onEnrollEffect(payload);
  }
);

export const onEnrollClick = createCommand<void>(
  'onEnrollClick',
  function* () {
    const currentAbonementOfferId = yield select(selectCurrentAbonementOfferId)
    yield onEnrollEffect(currentAbonementOfferId);
  }
);

export function* onEnrollEffect (id: id) {
  yield call(console.log, 'onEnroll', id);
};
