import { call, put, select } from "redux-saga/effects";
import { createCommand } from "../../common/utilities/createCommand";
import { id } from "../../common/types/domain";
import { DetailsDrawerShown, StudentProfileNavigation, studentModalSlice } from "../store/modal";
import { studentEditableSlice } from "../store/editableResources";
import { selectCurrentStudentName } from "../selectors/domain";
import { saveStudentName, updateStudentDomain } from "./studentProfilePageEffects";

export const startEditingStudentName = createCommand<void>(
  'startEditingStudentName',
  function* () {

    const currentName = yield select(selectCurrentStudentName);

    yield put(studentEditableSlice.actions.setEditingResource({name: currentName}));
    yield put(studentEditableSlice.actions.startEditingField('name'));
  }
);

export const changeSudentName = createCommand<string>(
  'changeSudentName',
  function* ({ payload }) {
    yield put(studentEditableSlice.actions.setEditingResource({name: payload}));
  }
);

export const confirmEditingSudentName = createCommand<void>(
  'confirmEditingSudentName',
  function* () {
    yield saveStudentName();
    yield put(studentEditableSlice.actions.reset());
    yield updateStudentDomain();
  }
);

export const navigateStudentProfileAbonements = createCommand<void>(
  'navigateStudentProfileAbonements',
  function* () {
    yield put(studentModalSlice.actions.setModalState({studentProfileNavigation: StudentProfileNavigation.abonements}));
  }
);

export const navigateStudentProfileSubjects = createCommand<void>(
  'navigateStudentProfileSubjects',
  function* () {
    yield put(studentModalSlice.actions.setModalState({studentProfileNavigation: StudentProfileNavigation.subjects}));
  }
);

export const drilldownAbonement = createCommand<id>(
  'drilldownAbonement',
  function* ({ payload }) {
    yield put(studentModalSlice.actions.setModalState({currentOwnAbonementId: payload}));
    yield put(studentModalSlice.actions.setModalState({detailsDrawerShown: DetailsDrawerShown.abonementPageForStudent}));
  }
);

export const drilldownSubject = createCommand<id>(
  'drilldownSubject',
  function* ({ payload }) {
    yield call(console.log, 'drilldownSubject', payload);
  }
);
